.error {
  text-align: center;
  margin: 5rem 0;

  &--code {
    font-size: 27.7rem;
    line-height: 1;
    color: #ffffff;
    -webkit-text-stroke: 3px var(--brand-color);
    text-stroke: 3px var(--brand-color);
    font-weight: 800;
  }

  &--title {
    font-size: 4.8rem;
    line-height: 1.6;
  }

  &--description {
    margin-top: 2em;
    font-size: 2rem;
    color: var(--text-color-secondary);
  }

  &--buttons {
    margin-top: 6rem;
  }

  @media (max-width: 991px) {
    &--code {
      font-size: 12.5rem;
    }

    &--title {
      font-size: 3.2rem;
    }

    &--description {
      font-size: 1.8rem;
    }

    &--buttons {
      margin-top: 5rem;
    }
  }
}