.settings {
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;

  &--block {
    margin-bottom: 4rem;
  }

  @media (max-width: 991px) {
    max-width: none;
  }
}

.settings-user-form {
  @extend .block-shadow;

  &--main {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }

  &--image {
    flex-shrink: 0;
    margin-inline-end: 4rem;
    width: 20rem;

    .feedback {
      text-align: center;
    }
  }

  &--user {
    flex-grow: 1;
    min-width: 0;
  }


  .form-group {
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }

  .form-buttons {
    margin-top: 2rem;

    .btn {
      width: 100%;
    }
  }

  @media (max-width: 991px) {

    &--main {
      flex-direction: column;
      align-items: center;

      > * {
        width: 100%;
      }
    }

    &--image {
      margin-inline-end: 0;
      margin-bottom: 2.4rem;
      width: auto;
    }
  }

}

.user-image {
  display: block;
  color: var(--text-color-secondary);
  text-align: center;

  $component: &;

  input {
    display: none;
  }

  label {
    display: block;
    cursor: pointer;
    width: max-content;
    margin-left: auto;
    margin-right: auto;

    &:hover {
      #{$component}--image {
        transform: scale(1.03);
      }

    }
  }

  &--image {
    display: block;
    margin-bottom: 0.2rem;
    transition: all 0.3s;

    img {

      --image-size: 10rem;

      display: block;
      width: var(--image-size);
      height: var(--image-size);
      object-fit: cover;
      border-radius: 50%;
      transition: box-shadow 0.3s;
    }
  }

  &--title {
    display: block;
    color: currentColor;
    font-size: 1.4rem;
    line-height: 1.7;
  }

  &.disabled,
  &[disabled] {
    pointer-events: none;
    opacity: 0.5;
    color: var(--text-color-secondary);
  }

}


.user-socials-form {
  @extend .block-shadow;

  &--notice {
    font-size: 1.6rem;
    line-height: 1.6;
    padding: 1rem 2rem;
    background: rgba(217, 4, 41, 0.05);
    border-radius: 3rem;
    margin-bottom: 2rem;
  }

  .form-buttons {
    margin-top: 2rem;
    .btn {
      width: 100%;
    }
  }
}

.user-socials-list {
  margin: 0 -2rem -2rem;
  width: calc(100% + 4rem);
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  list-style: none;
  padding: 0;

  &--item {
    padding: 0 2rem 2rem;
    width: 33.333333%;
  }

  @media (max-width: 991px) {
    flex-direction: column;
    margin: 0 0 -2rem;
    width: 100%;

    &--item {
      width: 100%;
      padding: 0;
      margin-bottom: 2rem;
    }

  }
}

.secure-settings {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  margin: 0 -2rem 4rem;
  width: calc(100% + 4rem);

  &:last-child {
    margin-bottom: 0;
  }

  &--block {
    @extend .block-shadow;

    margin: 0 2rem;
    flex: 1;
    min-width: calc(50% - 4rem);
  }

  &--title {
    margin-bottom: 2rem;
    font-size: 1.6rem;
    line-height: 1.6;
  }

  &--buttons {
    &-link {
      display: flex;
      align-items: center;

      &:after {
        display: block;
        content: '';
        background: url(../img/arrow-brand.svg) 50% 50% no-repeat;
        background-size: contain;
        width: 0.8rem;
        height: 1.4rem;
        margin-inline-start: 1rem;

        .rtl & {
          transform: scaleY(-1);
        }
      }

    }
  }

  @media (max-width: 991px) {
    flex-direction: column;
    margin: 0 0 2rem;
    width: 100%;

    &:last-child {
      margin-bottom: 0;
    }

    &--block {
      width: 100%;
      max-width: none;
      margin: 0 0 2rem;
    }
  }
}

.password-settings {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  margin: 0 -2rem 4rem;
  width: calc(100% + 4rem);

  &:last-child {
    margin-bottom: 0;
  }

  &--block {
    @extend .block-shadow;

    margin: 0 2rem;
    flex: 1;
    min-width: calc(50% - 4rem);
  }

  &--title {
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 1.6;
    margin-bottom: 2rem;
  }

  @media (max-width: 991px) {
    flex-direction: column;
    margin: 0 0 2rem;
    width: 100%;

    &:last-child {
      margin-bottom: 0;
    }

    &--block {
      width: 100%;
      max-width: none;
      margin: 0 0 2rem;
    }
  }
}

.password-settings-form {
  .form-group {
    margin-bottom: 2rem;
  }

  .form-buttons {
    .btn {
      width: 100%;
    }
  }
}

.authentification-form {
  &--title {
    font-size: 3.2rem;
    line-height: 1.6;
    font-weight: 500;
    text-align: center;
    margin-bottom: 0.5rem;
  }

  &--subtitle {
    text-align: center;
    font-size: 1.6rem;
    line-height: 1.5;
    color: var(--text-color-secondary);
    margin-bottom: 2rem;
  }

  .form-group {
    margin-bottom: 2rem;

    .help-block {
      font-size: 1.6rem;
      color: var(--text-color-secondary);
      margin-top: 0.5rem;
    }
  }

  .form-buttons {
    .btn {
      width: 100%;
    }
  }

  &_google {

    .form-group {
      > .control-label {
        margin-bottom: 2rem;
        font-size: 1.6rem;
      }
    }

  }

  @media (max-width: 991px) {
    &--title {
      font-size: 2.4rem;
    }
  }
}

.google-auth {
  margin-bottom: 2rem;

  &--image {
    width: 11rem;
    margin-bottom: 2rem;

    img {
      width: 100%;
      max-width: 100%;
    }

  }

  &--code {
    font-size: 1.6rem;
    line-height: 150%;
    overflow-wrap: break-word;
  }
}