.modal-backdrop {
  background: var(--text-color);

  &.show {
    opacity: 0.3;
  }
}

.modal {

  $modal: &;

  &-close {
    padding: 0.5rem;
    margin: 0;
    border: none;
    background: none;
    z-index: 1;
    cursor: pointer;
    margin-inline-start: auto;
    margin-inline-end: -1rem;
    flex-shrink: 0;

    &:after {
      content: '';
      display: block;
      width: 2.4rem;
      height: 2.4rem;
      background: url(../img/close.svg) 50% 50% no-repeat;
      background-size: contain;
    }
  }

  &-title {
    font-weight: 500;
    font-size: 2.4rem;
    overflow-wrap: break-word;
    min-width: 0;
  }

  &-dialog {

    > #{$modal}-close {
      position: absolute;
      top: 1rem;
      inset-inline-end: 1rem;
      margin-inline-end: 0;
      background: rgba(255, 255, 255, 0.8);
      border-radius: 50%;

      &:hover {
        background: #fff;
      }
    }

  }

  &-content {
    border: none;
    border-radius: 3rem;
  }

  &-header {
    position: relative;
    padding: calc(var(--block-padding) / 2)  var(--block-padding) 0;
    //padding-inline-end: 5rem;
    border: none;
    align-items: flex-start;
  }

  &-body {
    padding: var(--block-padding);
  }

  &-footer {
    padding: 0 var(--block-padding) var(--block-padding);
    border: none;
  }

  // mods

  &-image {

    &#{$modal}-dialog {
      max-width: 100vw;
      max-height: 100vh;
      text-align: center;

      #{$modal}-content {
        width: auto;
        height: auto;
        max-width: 90vw;
        max-height: 90vh;
        margin-left: auto;
        margin-right: auto;
      }

    }

  }

  // end mods


  @media (min-width: 576px) {
    &-dialog {
      max-width: none;
      margin: .5rem;
    }
  }

  @media (min-width: 992px) {
    &-dialog {
      max-width: 52rem;
      margin: 1.75rem auto;
    }

  }

}