.main-nav-section {
  width: 100%;
  background-color: #ffffff;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(5px);

  @media (min-width: 992px) {
    position: sticky;
    top: 0;
    z-index: 100;
  }

  .close-menu {
    display: none;
  }

  @media (max-width: 991px) {
    display: none;

    &.show {
      display: block;
      position: fixed;
      z-index: 501;
      top: 0;
      left: 0;
      width: 100%;
      max-width: 100%;
      overflow-x: hidden;
      overflow-y: auto;
      height: 100vh;
      background-color: #ffffff;
      padding: 8rem 2rem 2rem;
    }

    .close-menu {
      display: block;
      position: absolute;
      top: 1.3rem;
      inset-inline-end: 0.5rem;
      z-index: 210;
    }
  }
}

.mobile-user {
  margin-bottom: 4rem;
  text-align: center;

  .usercard {
    max-width: 100%;
    display: inline-flex;
    background: #FFFFFF;
    box-shadow: 0 0.4rem 3rem rgba(19, 37, 79, 0.07);
    border-radius: 2rem;
    padding: 1rem;
    min-width: 19rem;

    &--image {

      img {
        width: 2.4rem;
        height: 2.4rem;
      }

    }
  }

  @media (min-width: 992px) {
    display: none;
  }
}

.close-menu {
  border: none;
  margin: 0;
  padding: 1rem;
  display: block;
  background: none;

  &:after {
    display: block;
    content: '';
    width: 2.4rem;
    height: 2.4rem;
    font-size: 0;
    background: url(../img/mobile-menu-close.svg) 50% 50% no-repeat;
    background-size: contain;
  }

  span {
    display: none
  }
}

.main-nav {
  margin-left: auto;
  margin-right: auto;
  max-width: 108rem;
  width: 100%;
  padding: 1rem 0;

  @media (max-width: 991px) {
    max-width: none;
  }
}

.main-nav-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--text-color);
  list-style: none;
  width: calc(100% + 2rem);
  margin: 0 -1rem;
  padding: 0;

  $component: &;

  &--item {
    padding: 0 1rem;

    &-link {
      color: currentColor;
      font-size: 1.6rem;

      &:hover {
        color: currentColor;
      }

      &.new {
        position: relative;

        &:after {
          content: '';
          display: block;
          width: 0.5rem;
          height: 0.5rem;
          background-color: var(--brand-color);
          border-radius: 50%;
          position: absolute;
          right: -0.7rem;
          top: 50%;
          transform: translateY(-140%);
        }
      }
    }

    &.is-active {
      #{$component}--item-link {
        color: var(--brand-color);
      }
    }

    &_exit {
      display: none;
    }
  }

  @media (max-width: 991px) {
    flex-direction: column;

    &--item {
      padding: 0;

      &:not(:last-child) {
        margin-bottom: 3rem;
      }

      &-link {
        display: inline-block;
        padding: 1rem;

        &.new {
          &:after {
            right: 0.3rem;
          }
        }
      }

      &_button {
        display: none;
      }

      &_exit {
        display: block;
        margin: 3rem 0 5rem;
      }
    }
  }
}

.mobile-balance-buttons-list {
  list-style: none;
  margin: 0;
  padding: 2rem 0 0;
  display: flex;
  margin: 0 -1rem;

  &--item {
    flex: 1;
    padding: 0 1rem;
  }

}

.mobile-balance {
  background-color: #ffffff;
  box-shadow: var(--shadow);
  border-radius: 2rem;
  padding: 1rem;

  &--button {
    display: flex;
    align-items: center;
    width: 100%;

    &:before {
      content: '';
      display: block;
      width: 2.4rem;
      height: 2.4rem;
      background: url(../img/theme-icon-wallet.svg) 50% 50% no-repeat;
      background-size: contain;
      margin-inline-end: 1rem;
      flex-shrink: 0;
    }

  }

  &--value {
    color: var(--text-color-secondary);
    font-size: 1.4rem;
    flex-grow: 1;
    min-width: 0;

    span {
      overflow-wrap: break-word;
      max-width: 100%;
    }
  }

  &--buttons {

    .btn {
      padding-left: 1rem;
      padding-right: 1rem;
      width: 100%;
      max-width: 100%;
      overflow: hidden;
      -ms-text-overflow: ellipsis;
      text-overflow: ellipsis;
    }
  }

  @media (min-width: 992px) {
    display: none;
  }
}

.mobile-case-button {
  margin-top: 1.5rem;

  .btn {
    width: 100%;
  }

  @media (min-width: 992px) {
    display: none;
  }
}