#footer {

}

.footer {
  padding: 6rem 0;
  border-top: 1px solid #EDF2F4;

  &--container {
    max-width: 139rem;
    margin: 0 auto;
    padding: 0 1.5rem;
  }

  &--top {
    padding-bottom: 6rem;
  }

  &--nav {
    border-top: 1px solid #E8EBEF;
    border-bottom: 1px solid #E8EBEF;
    padding: 5rem 0;
  }

  &--description {
    text-align: left;
    font-size: 1.2rem;
    color: var(--text-color-secondary);
    padding-top: 6rem;

    .rtl & {
      text-align: unset;
    }
  }

  @media (max-width: 991px) {
    padding: 3.5rem 0;

    &--container {
      max-width: 100%;
    }

    &--top {
      padding-bottom: 3rem;
    }

    &--nav {
      padding: 2rem 0;
    }

    &--description {
      padding-top: 3rem;
    }
  }
}

.footer-logo {
  margin: 0 auto;
  display: block;
  max-width: 8.8rem;
  font-size: 0;
  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 0;
    padding-bottom: calc((42 / 88) * 100%);
    background: url(../img/logo-gray.svg) 0 0 no-repeat;
    background-size: contain;
  }
}

.footer-nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-color-secondary);
  font-size: 1.6rem;

  &--item {
    margin: 1rem 2.5rem;

    &-link {
      color: currentColor;
      text-decoration: none;

      &:hover {
        color: currentColor;
        text-decoration: underline;
      }
    }
  }

  @media (max-width: 991px) {
    flex-direction: column;

    &--item {
      margin: 1rem 0;
    }
  }
}