input[type=password]::-ms-reveal,
input[type=password]::-ms-clear {
  display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
  -moz-appearance: textfield;
}

.control-label {
  display: block;
  font-size: 1.6rem;
  color: var(--text-color);
  margin-bottom: 0.5em;
}

.form-control {
  border-color: var(--input-default-border);

  height: var(--input-height);
  border-radius: var(--input-border-radius);
  padding-inline: var(--input-padding-inline);
  font-family: var(--main-font);
  font-weight: normal;
  font-size: 1.6rem;
  color: var(--text-color);

  &::placeholder {
    color: var(--text-color-05);
  }

  &:focus {
    border-color: var(--text-color-secondary);

    &::placeholder {
      opacity: 0.5;
    }
  }

  .has-error & {
    border-color: var(--brand-color);
  }

  &.disabled,
  &[disabled] {
    pointer-events: none;
    opacity: 0.5;
  }

  &[readonly] {
    background-color: #F6F9FA;
  }

}

.checkbox {
  position: relative;
  color: var(--text-color);

  input[type="checkbox"] {
    display: none;

    &:checked {
      & + label {

        &:before {
          background: url(../img/icons/custom-checkbox.svg) center center no-repeat;
          background-size: 1.2rem 0.9rem;
        }
      }
    }

    &[disabled],
    &.disabled {

      & + label {
        opacity: 0.5;
        pointer-events: none;
      }

    }
  }

  label {
    margin: 0;
    display: inline-flex;
    align-items: flex-start;
    justify-content: flex-start;
    font-size: 1.6rem;
    line-height: 1.5;
    color: currentColor;

    a {
      color: currentColor;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }

    &:before {
      flex-shrink: 0;
      cursor: pointer;
      content: '';
      display: block;
      width: 2rem;
      height: 2rem;
      border-radius: 0.3rem;
      background: #FFFFFF;
      border: 1px solid var(--text-color-secondary);
      position: relative;
      top: 0.1em;
    }

    span {
      margin-inline-start: 2rem;
    }
  }

  .rtl & {
  }

  &.disabled,
  &[disabled] {
    label {
      opacity: 0.5;
      pointer-events: none;
    }
  }

}

.form-group {
  text-align: left;

  .rtl & {
    text-align: right;
  }

  .help-block,
  .feedback {
    padding-inline: var(--input-padding-inline);

    &_message {
      padding-inline: 0;
    }
  }

  ~ .help-block,
  ~ .feedback {
    padding-inline: 0;
  }

  &_captcha {
    > * {
      max-width: 100%;
      text-align: center;
    }
  }

  &_links {
    text-align: right;
    font-size: 1.5rem;

    .rtl & {
      text-align: left;
    }

  }

  &_flex {
    display: flex;
    align-items: center;
  }

  &_button {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    .form-group {
      flex-grow: 1;
      min-width: 0;
    }

    .form-group-button {
      flex-grow: 0;
      margin-inline-start: 1rem;

      .btn {
        white-space: nowrap;

        .icon-refresh {
          margin: -0.5rem 0;
        }
      }
    }


  }

  &_line {
    display: flex;
    align-items: center;
    justify-content: flex-start;

    .control-label {
      margin-bottom: 0;
      margin-inline-end: 2rem;
    }
  }

  @media (max-width: 991px) {
    &_button {
      flex-direction: column;
      > * {
        width: 100%;
      }

      .form-group-button {
        margin-inline-start: 0;
        margin-top: 0.5rem;

        .btn {
          width: 100%;

        }
      }
    }

    &_line {
      flex-direction: column;
      align-items: flex-start;

      .control-label {
        margin-inline-end: 0;
        margin-bottom: 2rem;
      }
    }
  }

}

.feedback {
  font-size: 1.2rem;
  line-height: 1.5;
  color: var(--text-color-secondary);

  .has-error & {
    color: var(--brand-color);
  }

  .has-success & {
    color: var(--success-color);
  }

}

.help-block {
  color: var(--text-color-secondary);
  font-size: 1.2rem;
  line-height: 1.5;

  &_message {
    font-size: 1.4rem;
    color: var(--text-color);
    margin-top: 1rem;

    span {
      color: var(--brand-color);
    }
  }
}

.form-field {
  position: relative;

  .control-label {
    position: absolute;
    z-index: 2;
    inset-inline-start: var(--input-padding-inline);
    top: 50%;
    transform-origin: left center;
    transform: translateY(-50%);
    transition: all 0.3s;
    white-space: nowrap;
    max-width: calc(100% - var(--input-padding-inline)*2);
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    color: var(--text-color-secondary);
  }

  .form-control {
    position: relative;
    z-index: 1;
    padding-top: 1.7rem;
    &::placeholder {
      opacity: 0;
    }

    &:focus,
    &:not(:placeholder-shown) {

      + .control-label {
        transform: translateY(-100%) scale(0.75);
      }

    }

    &:placeholder-shown {

    }
  }
  textarea {
    &.form-control {

      &:focus,
      &:not(:placeholder-shown) {

        + .control-label {
          transform: translateY(-100%) scale(0.75);
        }

      }

      + .control-label {
        top: 2.3rem;
      }
    }
  }

  &_button {
    .form-control {
      padding-inline-end: 5rem;
    }
  }
}

.show-pass-button {
  cursor: pointer;
  display: block;
  position: absolute;
  font-size: 0;
  top: 50%;
  inset-inline-end: 2.3rem;
  transform: translate(0, -50%);
  background: url(../img/icons/pass-eye.svg) center center no-repeat;
  background-size: contain;
  width: 2.4rem;
  height: 2.4rem;
  z-index: 3;
  outline: 0;

  &:after {
    content: '';
    display: block;
    background: url(../img/icons/pass-eye-cross.svg) center center no-repeat;
    background-size: contain;
    width: 2.4rem;
    height: 2.4rem;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 4;
    transition: opacity 0.3s;
    opacity: 1;
  }

  &.show {
    &:after {
      opacity: 0;
    }
  }
}

.double-radio {
  display: flex;
  align-items: stretch;
  background: var(--items-border-color);
  border-radius: 1.2rem;

  &--item {
    flex: 1;

    input[type="radio"] {
      display: none;

      + label {

      }

      &:checked {
        + label {
          background-color: #ffffff;
          border-radius: 1.2rem;
          box-shadow: 0 0.8rem 4rem rgba(19, 37, 79, 0.07);
        }
      }

      &[disabled],
      &.disabled {

        & + label {
          opacity: 0.5;
          pointer-events: none;
        }

      }
    }

    label {
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 500;
      font-size: 1.6rem;
      padding: 1rem 0.5rem;
      cursor: pointer;
      transition: background-color 0.15s;
      height: 100%;

      .icon {
        margin-inline-end: 1rem;
        width: 1.25em;
        height: 1.25em;
      }
    }

    &.disabled,
    &[disabled] {

      label {
        opacity: 0.5;
        pointer-events: none;
      }
    }

  }

  @media (max-width: 991px) {
    &--item {
      label {
        font-size: 1.4rem;

        .icon {
          margin-inline-end: 0.5rem;
        }
      }
    }
  }
}


.upload-file {

  input {
    display:none;
  }

  label {
  }

  &.disabled,
  &[disabled] {

    label {
      opacity: 0.5;
      pointer-events: none;
    }
  }

}

.list-filename {
  position: absolute;
  top: 50%;
  left: 2.5%;
  transform: translateY(-50%);
  text-align: center;
  z-index: 3;
  width: 95%;
  color: var(--text-color);
  text-shadow: 1px -1px 0 #fff, 1px 1px 0 #fff, -1px 1px 0 #fff, -1px -1px #fff;
  max-height: 95%;
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  overflow-wrap: break-word;
  font-size: 1.6rem;

  @media (max-width: 991px) {
    font-size: 1.4rem;
  }
}

.pdf-sign {
  &:after {
    content: 'pdf';
    position: absolute;
    z-index: 2;
    bottom: 0.5rem;
    inset-inline-end: 0.5rem;
    text-transform: uppercase;
    text-shadow: 1px -1px 0 #fff, 1px 1px 0 #fff, -1px 1px 0 #fff, -1px -1px #fff;
    font-size: 2rem;
    font-weight: 500;
    color: var(--text-color-secondary);
  }


  @media (max-width: 991px) {
    &:after {
      font-size: 1.6rem;
    }
  }
}

.uploads-list {
  list-style: none;
  margin: -1rem;
  padding: 0;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;

  &--item {
    margin: 1rem;
    position: relative;
    $item: &;

    &:hover {
      #{$item}-remove {
        opacity: 1;
      }
    }

    &-link {
      display: block;
      position: relative;
      z-index: 1;

      img {
        width: 10rem;
        height: 10rem;
        object-fit: cover;
        position: relative;
        z-index: 1;

        &[src*=".svg"] {
          object-fit: contain;
        }
      }


      &--title {
        @extend .list-filename;
      }

      &[href$=".pdf"] {
        @extend .pdf-sign;
      }
    }

    &-remove {
      font-size: 0;
      position: absolute;
      z-index: 5;
      top: 0.5rem;
      width: 2.4rem;
      height: 2.4rem;
      inset-inline-end: 0.5rem;
      background: rgba(0, 0, 0, 0.5) url(../img/remove-image.svg) 50% 50% no-repeat;
      background-size: contain;
      border-radius: 50%;
      opacity: 0.5;
      transition: opacity 0.3s;

      &:hover {
        opacity: 1;
      }
    }
  }

  @media (max-width: 991px) {
    &--item {
      &-remove {
        opacity: 1;
      }
    }
  }
}