.support {
  &--block {
    &:not(:last-child) {
      margin-bottom: 2.4rem;
    }
  }

  @media (max-width: 991px) {
    &--block {
      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }
  }
}

.support-form {
  @extend .block-shadow;

  &--title {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.6;
    margin-bottom: 4rem;
  }

  .form-group {
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }

  textarea {
    &.form-control {
      height: 24.2rem;
      resize: vertical;
    }
  }

  .form-buttons {
    text-align: center;
    margin-top: 4rem;

    .btn {
      min-width: 23rem;
    }
  }

  @media (max-width: 991px) {
    &--title {
      font-size: 1.8rem;
      text-align: center;
    }
  }
}

.support-tickets {
  @extend .block-shadow;

  &--title {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.6;
    margin-bottom: 4rem;
  }

  @media (max-width: 991px) {
    &--title {
      font-size: 1.8rem;
      text-align: center;
    }
  }
}

.tickets-list {
  margin: 0;
  padding: 0;
  list-style: none;

  &--item {
    padding: 4rem 0;
    border-bottom: 1px solid var(--items-border-color);

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
      border: none;
    }
  }
}


.tickets-item {
  $component: &;
  line-height: 1.6;

  &--main {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 2rem;
  }

  &--header {
    flex-grow: 1;
    min-width: 0;
    margin-inline-end: 2rem;
  }

  &--meta {
    max-width: 50%;
    text-align: right;
    flex-shrink: 0;

    .rtl & {
      text-align: left;
    }
  }

  &--title {
    font-size: 1.8rem;
    color: var(--text-color);
    margin-bottom: 0.5rem;

    a {
      color: currentColor;

      &:hover {
        color: currentColor;
      }
    }
  }

  &--date {
    color: var(--text-color-secondary);
  }

  &--status {
    font-size: 1.8rem;
    margin-bottom: 0.5rem;
  }

  &--division {
    color: var(--text-color-secondary);
  }

  &--buttons {
    text-align: center;

    .btn {
      min-width: 23rem;
    }
  }

  &_new {
    #{$component}--title {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      color: var(--brand-color);

      &:before {
        content: '';
        display: block;
        margin-inline-end: 0.8rem;
        background: url(../img/new-message.svg) 50% 50% no-repeat;
        background-size: contain;
        width: 2rem;
        height: 1.6rem;
      }
    }
  }

  @media (max-width: 991px) {
    &--main {
      flex-direction: column;
    }

    &--header {
      margin-inline-end: 0;
      margin-bottom: 2rem;
    }

    &--meta {
      max-width: none;
      text-align: left;

      .rtl & {
        text-align: right;
      }
    }
  }
}

.ticket-status {
  &.open {
    color: var(--brand-color);
  }
  &.closed {
    color: var(--success-color);
  }
}

.support-ticket {

  &--nav {
    margin-bottom: 2.6rem;
  }

  &--header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 4rem;
  }

  &--title {
    flex-grow: 1;
    min-width: 0;
    font-weight: 500;
    font-size: 3.2rem;
    line-height: 1.6;
  }

  &--controls {
    flex-shrink: 0;
    margin-inline-start: 2rem;
  }

  &--content {
    margin-bottom: 4rem;
  }

  &--form {

  }

  @media (max-width: 991px) {

    &--header {
      flex-direction: column;
      margin-inline-start: 0;
      margin-top: 4rem;
    }

    &--controls {
      margin-inline-start: 0;
      margin-top: 4rem;

      .btn {
        font-size: 1.4rem;
        padding-left: 0.5rem;
        padding-right: 0.5rem;
      }

    }

    &--title {
      font-size: 2.4rem;
      text-align: center;
    }
  }
}


.ticket-messages-list {
  list-style: none;
  padding: 0;
  margin: 0;

  &--item {
    margin-bottom: 4rem;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @media (max-width: 991px) {

    &--item {
      margin-bottom: 3rem;
    }
  }

}

.ticket-message {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  &--main {
    flex-grow: 1;
    min-width: 0;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }

  &--meta {
    flex-shrink: 0;
    margin-inline-start: 2rem;
  }

  &--image {
    flex-shrink: 0;
    margin-inline-end: 2rem;

    img {
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
      object-fit: cover;
    }
  }

  &--container {

  }

  &--name {
    font-weight: 500;
    font-size: 1.8rem;
    max-width: 100%;
    overflow-wrap: break-word;
    margin-bottom: 1rem;

    &_support {
      color: var(--brand-color);
    }
  }

  &--content {
    font-size: 1.8rem;
    overflow-wrap: break-word;
  }

  &--date {
    color: var(--text-color-secondary);
  }

  @media (max-width: 991px) {
    flex-direction: column;

    &--main {

    }

    &--meta {
      order: -1;
      margin-inline-start: 0;
      margin-bottom: 2rem;
    }

  }

}

.message-images-list {
  list-style: none;
  padding: 0;
  margin: 0.5rem -1rem;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;

  &--item {
    margin: 1rem;

    &-link {
      display: block;
      position: relative;
      transition: all 0.3s;

      &:hover {
        transform: scale(1.03);
      }

      img {
        max-width: 20rem;
        max-height: 20rem;
        object-fit: cover;

        position: relative;
        z-index: 1;

        &[src*=".svg"] {
          width: 15rem;
          height: 15rem;
          object-fit: contain;
        }
      }

      &--title {
        @extend .list-filename;
      }

      &[href$=".pdf"] {
        @extend .pdf-sign;
      }
    }

  }

  @media (max-width: 991px) {
    &--item {

      &-link {

        img {
          max-width: 10rem;
          max-height: 10rem;
        }

      }

    }

  }
}

.support-ticket-form {

  .form-group {
    &:not(:last-child) {
      margin-bottom: 2rem;
    }
  }

  textarea {
    &.form-control {
      height: 24.2rem;
      resize: vertical;
    }
  }

  .form-buttons {
    .btn {
      min-width: 24rem;
    }
  }

}