html {
  font-size: calc(10vw / 16.70);
  scroll-behavior: smooth;

  @media (min-width: 1670px) {
    font-size: 10px;
  }

  @media (max-width: 991px) {
    font-size: calc(10vw / 3.75);
  }

  @media (max-width: 991px) and (orientation: landscape) {
    font-size: calc(10vh / 3.75);
  }
}

body,
html {
  -webkit-font-smoothing: antialiased;
}

body {
  direction: ltr;

  font-size: 1.6rem;
  line-height: 1.3;
  font-weight: 400;
  font-family: var(--main-font);
  color: var(--text-color);
  min-width: 320px;
  max-width: 100%;
  overflow-x: hidden;
}

.rtl {
  direction: rtl;
  text-align: unset;
}

.menu-open {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: var(--brand-color);

  &:hover {
    color: var(--brand-color);
    text-decoration: underline;
  }

  &.disabled,
  &[disabled] {
    opacity: 0.5;
    pointer-events: none;
  }
}


#wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  //background-color: #ffd6d6;
  text-align: center;
  max-width: 100%;

  @media (max-width: 991px) {
    //overflow-x: hidden;
    max-width: 100%;
  }
}

#header {
  flex-shrink: 0;
  max-width: 100%;

  @media (max-width: 992px) {
    position: sticky;
    top: 0;
    z-index: 201;
  }
}

#content {
  flex-grow: 1;
  min-width: 0;
  //background-color: #fff8d6;
  padding: 3.4rem 0 4.4rem;
  max-width: 100%;

  @media (max-width: 992px) {
    overflow-x: hidden;
  }
}

#container {
  max-width: 167rem;
  text-align: left;
  margin: 0 auto;
  padding: 0 1.5rem;

  .rtl & {
    text-align: unset;
  }

  @media (max-width: 991px) {
    max-width: 100%;
  }
}

#footer {
  flex-shrink: 0;
}

.block-shadow {
  position: relative;
  background-color: #ffffff;
  box-shadow: var(--shadow);
  padding: var(--block-padding);
  border-radius: var(--bodrder-radius);
}

.block-shadow-lg {
  position: relative;
  background-color: #ffffff;
  box-shadow: var(--shadow-lg);
  padding: var(--block-padding);
  border-radius: var(--bodrder-radius);

  @media (max-width: 767px) {
    padding: var(--block-padding-sm);
  }
}

.main-content {
  padding-top: 4rem;
  max-width: 108rem;
  margin-left: auto;
  margin-right: auto;
}

.secondary-content {
  padding-top: 2rem;
}