.referal-lint-section {
  margin-bottom: 5rem;
}

.referal-link {

  .form-group {

    &_button {

      .form-group-button {
        margin-inline-start: 4rem;
      }
    }


  }

  @media (max-width: 991px){

    .form-group {

      &_button {
        .form-group-button {
          margin-top: 2rem;
          margin-inline-start: 0;
        }
      }

    }

  }

}
