:root {

  --brand-color: #D90429;
  --success-color: #1DB96E;
  --error-color: #D90429;
  --danger-color: #d94f04;
  --blue-color: #4D8FF4;
  --text-color: #2B2D42;
  --text-color-secondary: #8D99AE;
  --text-color-05: #9596A1;

  --main-font: Rubik, sans-serif;

  --shadow-sm: 0 1.2rem 4rem rgba(19, 37, 79, 0.17);
  --shadow: 0 1.2rem 4rem rgba(19, 37, 79, 0.07);
  --shadow-lg: 0 4rem 4rem rgba(19, 37, 79, 0.06);

  --bodrder-radius: 3rem;
  --block-padding: 3rem;
  --block-padding-sm: 2rem;

  --input-default-border: #C6CCD7;
  --input-active-border: #8D99AE;
  --input-height: 5rem;
  --input-border-radius: 1.2rem;
  --input-padding-inline: 2rem;

  --button-height: 5rem;
  --button-border-radius: 1.2rem;
  --button-disabled-opacity: 0.3;

  --items-border-color: #EDF2F4;

  @media (max-width: 991px) {
    --block-padding: 2rem;
  }

}