.btn {
  font-weight: 500;
  color: var(--text-color);
  font-size: 1.6rem;
  line-height: 1.1;
  padding: 1.52rem 4rem;
  border-radius: var(--button-border-radius);
  border: 1px solid var(--brand-color);
  transition: all 0.3s;
  text-decoration: none;

  &.disabled,
  &[disabled] {
    opacity: var(--button-disabled-opacity);
    pointer-events: none;
  }

  &:hover {
    transform: scale(1.039);
    text-decoration: none;
  }

  &-primary {
    background: var(--brand-color);
    color: #ffffff;

    &:hover {
      background: var(--brand-color);
      border-color: var(--brand-color);
    }

    &:active,
    &:focus {
      background: var(--brand-color);
      border-color: var(--brand-color);
    }

    &.disabled,
    &[disabled] {
      border-color: var(--brand-color);
      background: var(--brand-color);
    }

  }

  &-sm {
    padding: 1.02rem 2.5rem;
  }

  &.copied {
    position: relative;

    &:after {
      content: '';
      display: block;
      position: absolute;
      z-index: 10;
      inset-inline-end: 1rem;
      top: 50%;
      transform: translateY(-50%);
      width: 1.8rem;
      height: 1.8rem;
      background: url(../img/icons/check-green.svg) center center no-repeat;
      background-size: contain;
      animation: copyButtonSuccess 3s linear 1 running;
      //animation-duration: 3s;
      //animation-iteration-count: 1;
      //animation-play-state: running;
      opacity: 0;
    }

  }

  @media (max-width: 991px) {
    &:hover {
      transform: none;
    }
  }
}

@keyframes copyButtonSuccess {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
