.registration {
  margin-top: 6.2%;

  &--header {
    margin-bottom: 11.4rem;
  }

  &--content {
    text-align: center;
  }

  &--footer {
    margin-top: 8rem;
    text-align: center;
  }

  @media (max-width: 991px) {
    margin-top: 5rem;

    &--header {
      margin-bottom: 4rem;
    }

  }

  &--activation-message {
    text-align: center;

    &:not(:last-child) {
      margin-bottom: 1rem;
    }
  }
}

.reg-title {
  font-weight: 500;
  font-size: 3.2rem;
  line-height: 1.1;
  text-align: center;
  margin-bottom: 2rem;

  @media (max-width: 991px) {
    font-size: 2.4rem;
  }
}

.registration-form {
  width: 100%;
  max-width: 72.4rem;
  margin: 0 auto;
  text-align: left;

  .rtl & {
    text-align: right;
  }

  @extend .block-shadow;

  &--title {
    @extend .reg-title;
  }

  &--fields {
    margin: 0 -1rem;
    width: calc(100% + 2rem);
    display: flex;
    flex-wrap: wrap;

    .form-group {
      padding: 0 1rem;
      width: 50%;

      &:not(:last-child) {
        margin-bottom: 2rem;
      }

      &_captcha {
        width: 100%;
      }

      &_fw {
        width: 100%;
      }
    }

    .form-buttons {
      width: 100%;
      padding: 0 1rem;

      &:not(:last-child) {
        margin-bottom: 2rem;
      }

      .btn {
        width: 100%;
      }
    }

  }

  .help-block {
    &_message {
      color: var(--blue-color);
    }
  }

  @media (max-width: 991px) {
    &--fields {
      flex-direction: column;

      .form-group {
        width: 100%;
      }
    }
  }

  @media (max-width: 767px) {
    max-width: none;
  }

}

.login-form {
  width: 100%;
  max-width: 38.2rem;
  margin: 0 auto;
  text-align: left;

  .rtl & {
    text-align: right;
  }

  @extend .block-shadow;

  &--title {
    @extend .reg-title;
  }

  &--fields {

    .form-group {
      &:not(:last-child) {
        margin-bottom: 2rem;
      }
    }

    .form-buttons {
      &:not(:last-child) {
        margin-bottom: 2rem;
      }

      .btn {
        width: 100%;
      }
    }

  }

  @media (max-width: 767px) {
    max-width: none;
  }

}

.registration-confirmation {
  &--title {
    @extend .reg-title;
  }

  &--description {
    font-size: 18px;
    color: var(--text-color-secondary);
  }

}