.notice {
  display: inline-block;
  border-radius: 100px;
  line-height: 1.2;
  font-size: 1.6rem;
  text-align: center;
  padding: 0.8rem 1rem;
  color: var(--text-color);
  background: rgba(100, 100, 100, 0.15);

  &_success {
    background: rgba(29, 185, 110, 0.15);
  }
  &_error {
    background: rgba(217, 4, 41, 0.15);
  }
}

.dropdown-menu {
  border-radius: var(--bodrder-radius);
  box-shadow: var(--shadow);
  color: var(--text-color);
  font-size: 1.6rem;
  border: none;

  a,
  .dropdown-item {
    color: currentColor;
    padding: 1rem 2rem;

    &:hover {
      text-decoration: none;
      background: none;
      opacity: 0.8;
    }

    &.active {
      color: var(--brand-color);
      background: none;
    }
  }
}

.usercard {
  display: flex;
  align-items: center;
  text-align: left;

  .rtl & {
    text-align: right;
  }

  &--image {
    flex-shrink: 0;
    margin-inline-end: 2rem;
    img {
      display: block;
      border: none;
      width: 5rem;
      height: 5rem;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &--content {
    flex-grow: 1;
    min-width: 0;
  }

  &--nickname {
    color: var(--text-color);
    font-weight: 500;
    font-size: 1.6rem;
    line-height: 1.1;
    white-space: nowrap;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    max-width: 100%;

    &:not(:last-child) {
      margin-bottom: 1rem;
    }

  }

  &--name {
    white-space: nowrap;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    font-weight: 400;
    font-size: 1.4rem;
    line-height: 1.1;
    max-width: 100%;
    color: var(--text-color-secondary);
  }
}

.balance-block {
  display: flex;
  align-items: center;
  text-align: left;

  .rtl & {
    text-align: right;
  }

  &--balance {
    display: flex;
    margin-inline-end: 4rem;

    &:before {
      content: '';
      display: block;
      flex-shrink: 0;
      background: url(../img/theme-icon-wallet.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 4rem;
      height: 4rem;
      margin-inline-end: 4rem;
    }

    &-content {
      font-weight: 500;
      font-size: 1.6rem;
      color: var(--text-color-secondary);
    }
  }

  &--buttons {
    margin: 0 -1rem;
    display: flex;

    .btn {
      margin: 0 1rem;
      min-width: 18rem;
    }
  }

  @media (max-width: 991px) {

  }
}

.page-title {
  text-align: center;
  font-weight: 500;
  font-size: 3.2rem;
  margin-bottom: 4rem;

  @media (max-width: 991px) {
    font-size: 2.4rem;
    margin-bottom: 2rem;
  }
}

.operation-status {
  color: var(--text-color-secondary);

  &.ok {
    color: var(--success-color);
  }

  &.in-progress {
    color: var(--danger-color);
  }

  &.error,
  &.canceled,
  &.denied {
    color:var(--error-color);
  }
}

.pagination-wrapper {
  margin: 4rem 0;
  position: relative;

  @media (max-width: 991px) {
    margin: 2rem 0;
  }
}

.pagination {
  padding: 0;
  margin: -0.5rem 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  > li {
    color: var(--text-color);
    margin: 0.5rem 1rem;

    a,
    span {
      display: flex;
      align-items: center;
      justify-content: center;
      background: #FFFFFF;
      box-shadow: 0 0.3rem 0.7rem rgba(43, 45, 66, 0.15);
      border-radius: 50%;
      width: 4rem;
      height: 4rem;
      font-size: 1.8rem;
      color: currentColor;
      line-height: 1;
      transition: all 0.3s;

      &:hover {
        text-decoration: none;
        transform: translate(-0.2rem, -0.2rem);
      }
    }

    span {
      opacity: 0.5;
    }

    &.prev,
    &.next {
      a,
      span {
        font-size: 0;
        box-shadow: none;
        background: none;

        &:after {
          content: '';
          display: block;
          background: url(../img/arrow.svg) 50% 50% no-repeat;
          background-size: contain;
          width: 0.8rem;
          height: 1.4rem;
        }

      }

      span {
        &:hover {
          transform: none;
        }
      }
    }

    &.prev {
      a,
      span {
        &:after {
          transform: scaleX(-1);
        }
      }
    }

    &.active {
      color: var(--brand-color);
    }

  }

  @media (max-width: 991px) {
    justify-content: flex-start;
  }
}

.tabs-menu {
  display: flex;
  align-items: stretch;
  background: var(--items-border-color);
  border-radius: 1.2rem;
  color: var(--text-color);

  &--item {
    flex: 1;

    $item: &;

    &-link {
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 500;
      font-size: 1.6rem;
      padding: 1rem 0.5rem;
      cursor: pointer;
      transition: background-color 0.15s;
      height: 100%;
      color: currentColor;
      white-space: nowrap;

      &:hover {
        color: var(--brand-color);
        text-decoration: none;
      }

      .icon {
        margin-inline-end: 1rem;
        width: 1.25em;
        height: 1.25em;
      }
    }

    &.disabled,
    &[disabled] {
      opacity: 0.5;
      pointer-events: none;
    }

    &.is-active {
      #{$item}-link {
        background-color: #ffffff;
        border-radius: 1.2rem;
        box-shadow: 0 0.8rem 4rem rgba(19, 37, 79, 0.07);
      }
    }

  }

  @media (max-width: 991px) {
    &--item {
      &-link {
        font-size: 1.4rem;

        .icon {
          margin-inline-end: 0.5rem;
        }
      }
    }
  }
}

.back-link {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  font-size: 1.4rem;
  line-height: 1;

  &:before {
    content: '';
    display: block;
    margin-inline-end: 0.64em;
    width: 0.57em;
    height: 1em;
    background: url(../img/arrow-brand.svg) 50% 50% no-repeat;
    background-size: contain;
    transform: scaleX(-1);

    .rtl & {
      transform: none;
    }
  }
}

@keyframes loading {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(1turn);
  }
}

.loading {
  position: relative;

  &.btn {
    color: transparent;
    pointer-events: none;

    &:after {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      background: url(../img/loader.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 2rem;
      height: 2rem;
      margin-left: -1rem;
      margin-top: -1rem;
      transform-origin: center center;
      animation-name: loading;
      animation-duration: 1.5s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
      animation-play-state: running;
    }

    &-primary {
      &:after {
        background: url(../img/loader-w.svg) 50% 50% no-repeat;
        background-size: contain;
      }
    }
  }
}