.landing-decoration {
  position: relative;
  overflow: hidden;
}

@keyframes landing-decoration-scale {
  100% {
    transform: scale(1.2);
  }
}
@keyframes landing-decoration-color {
  100% {
    fill: #8D99AE;
  }
}
@keyframes landing-decoration-color2 {
  100% {
    fill: #2B2D42;
  }
}
.landing-decoration-left {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 74rem;
  height: 102rem;
  transform-origin: left top;
  animation: landing-decoration-scale 55s infinite alternate;
}
.landing-decoration-left path {
  animation: landing-decoration-color 30s infinite alternate;
}
@media (max-width: 991px) {
  .landing-decoration-left {
    width: 43rem;
    height: 59rem;
    top: -9rem;
    left: -9rem;
    transform: rotate(30deg);
  }
}

.landing-decoration-right {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 78rem;
  height: 62rem;
  z-index: -1;
  transform-origin: right bottom;
  animation: landing-decoration-scale 40s infinite alternate;
}
.landing-decoration-right path {
  animation: landing-decoration-color2 20s infinite alternate;
}
@media (max-width: 991px) {
  .landing-decoration-right {
    bottom: -3.5rem;
    right: -7rem;
    width: 44.3rem;
    height: 35.3rem;
    transform: rotate(10deg);
  }
}