.gtng-logo {
  margin: 0 auto;
  display: block;
  width: 8.7rem;
  font-size: 0;

  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 0;
    padding-bottom: calc((43 / 87) * 100%);
    background: url(../img/logo.svg) 0 0 no-repeat;
    background-size: contain;
  }

  &_reg {
    width: 100%;
    max-width: 27.9rem;
  }

  @media (max-width: 991px) {

    &_reg {
      max-width: 15.4rem;
    }

  }
}

#header {

}

.header {
  width: 100%;
  border-bottom: 1px solid #EDF2F4;
  background-color: #ffffff;

  &--container {
    width: 100%;
    max-width: 167rem;
    margin: 0 auto;
    padding: 0 1.5rem;
  }

  &--content {
    padding: 2.7rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &--logo {
    flex-shrink: 0;

  }

  &--balance-bar {
    margin-inline-start: 5rem;
    margin-inline-end: auto;
    border-radius: 20px;
    box-shadow: var(--shadow);
    padding: 1.2rem 2rem;
  }

  &--userbar {
    display: flex;
    align-items: center;
    margin-inline-start: auto;
  }

  &--lang {

  }

  &--user {
    margin-inline-start: 4rem;
  }

  &--buttons {
    display: flex;
    margin-inline-start: 6rem;

    .btn {
      &:not(:last-child) {
        margin-inline-end: 2rem;
      }
    }

    @media (max-width: 991px) {

    }
  }

  &--mobile-button {
    display: none;
  }

  @media (max-width: 991px) {
    max-width: none;


    &--content {
      padding: 2rem 0;
    }

    &--buttons {
      display: none;
    }

    &--balance-bar {
      display: none;
    }

    &--user {
      display: none;
    }

    &--mobile-button {
      display: block;
      margin-inline-start: 2rem;
    }

  }
}

.lang-dropdown {
  &--button {
    appearance: none;
    background: none;
    padding: 0;
    margin: 0;
    border: none;
    display: flex;
    align-items: center;
    color: var(--text-color);

    &:after {
      content: '';
      display: block;
      margin-inline-start: 1rem;
      background: url(../img/caret-down.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 1.4rem;
      height: 1rem;
      transition: all 0.3s;
    }

    &.show {
      &:after {
        transform: rotate(0.5turn);
      }
    }
  }

  .dropdown-item {
    text-align: center;
  }

}

.user-dropdown {
  .dropdown-menu {
    min-width: 100%;
    border-radius: 2rem;
  }

  &--button {
    position: relative;
    appearance: none;
    background: none;
    padding: 0;
    margin: 0;
    border: none;
    border-radius: 2rem;
    box-shadow: var(--shadow);
    padding-block: 1rem;
    padding-inline-start: 2rem;
    padding-inline-end: 2rem;
    display: flex;
    align-items: center;
    max-width: 30rem;

    &:after {
      content: '';
      display: block;
      flex-shrink: 0;
      background: url(../img/caret-down.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 1.4rem;
      height: 1rem;
      transition: all 0.3s;
      margin-inline-start: 2rem;
    }

    &.show {
      &:after {
        transform: rotate(0.5turn);
      }
    }

    .usercard {
      flex-grow: 1;
      min-width: 0;
    }
  }
}

.mobile-menu-button {
  display: block;
  appearance: none;
  padding: 1rem;
  background: none;
  margin: 0;
  margin-inline-end: -1rem;
  border: none;
  font-size: 0;

  &:after {
    display: block;
    content: '';
    background: url(../img/mobile-menu-button.svg) 50% 50% no-repeat;
    background-size: contain;
    width: 2.4rem;
    height: 2.4rem;
  }
}