/* rubik-500 - latin_cyrillic */
@font-face {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 500;
  src: url('../fonts/rubik-v19-latin_cyrillic-500.eot'); /* IE9 Compat Modes */
  src: local(''),
  url('../fonts/rubik-v19-latin_cyrillic-500.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
  url('../fonts/rubik-v19-latin_cyrillic-500.woff2') format('woff2'), /* Super Modern Browsers */
  url('../fonts/rubik-v19-latin_cyrillic-500.woff') format('woff'), /* Modern Browsers */
  url('../fonts/rubik-v19-latin_cyrillic-500.ttf') format('truetype'), /* Safari, Android, iOS */
  url('../fonts/rubik-v19-latin_cyrillic-500.svg#Rubik') format('svg'); /* Legacy iOS */
}

