.dashboard-stats {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  justify-content: space-between;

  &--header {
    width: 100%;
    margin-bottom: 2rem;
  }

  &--title {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.6;
  }

  &--block {
    width: calc(50% - 2rem);
    margin-bottom: 4rem;
    //position: relative;

    &_bg {
      @extend .block-shadow-lg;
    }
  }

  @media (max-width: 991px) {
    flex-direction: column;

    &--title {
      font-size: 1.8rem;
    }

    &--block {
      width: 100%;
    }
  }
}

.cases-shop {

  &--buttons {
    margin-top: 3rem;
    .btn {
      width: 100%;
    }
  }

  @media (max-width: 991px) {
    &--buttons {
      margin-top: 2rem;
    }
  }
}

.cases-shop-list {
  list-style: none;
  width: calc(100% + 2rem);
  margin: 0 -1rem;
  padding: 0;
  display: flex;
  align-items: stretch;
  justify-content: space-between;

  &--item {
    width: 25%;
    padding: 0 1rem;
  }

  @media (max-width: 991px) {
    margin: 0 -0.5rem;

    &--item {
      padding: 0 0.5rem;
    }
  }
}

.cases-shop-item {
  background: #FFFFFF;
  box-shadow: var(--shadow);
  border-radius: 1.2rem;
  display: block;
  padding: 1.7rem 0.5rem;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 1;
  color: var(--text-color);
  white-space: nowrap;
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  text-align: center;

  &:hover {
    text-decoration: none;
  }

}

.stat-block {
  display: flex;

  align-items: flex-start;
  justify-content: flex-start;

  --stats-icon-size: 6rem;

  &:before {
    content: '';
    display: block;
    flex-shrink: 0;
    width: var(--stats-icon-size);
    height: var(--stats-icon-size);
    margin-inline-end: 4rem;
  }

  &--content {
    flex-grow: 1;
    min-width: 0;
  }

  &--title {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.7;
    margin-bottom: 1rem;
  }

  &--value {
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 1.7;
    color: var(--brand-color);
  }

  &_total-in {
    &:before {
      background: url(../img/theme-icon-summ.svg) 50% 50% no-repeat;
      background-size: contain;
    }
  }
  &_total-profit {
    &:before {
      background: url(../img/theme-icon-chart.svg) 50% 50% no-repeat;
      background-size: contain;
    }
  }
  &_team {
    &:before {
      background: url(../img/theme-icon-team.svg) 50% 50% no-repeat;
      background-size: contain;
    }
  }
  &_team-sm {
    --stats-icon-size: 4rem;

    &:before {
      background: url(../img/theme-team.svg) 50% 50% no-repeat;
      background-size: contain;
    }
  }
  &_level-sm {
    --stats-icon-size: 4rem;

    &:before {
      background: url(../img/theme-team-level.svg) 50% 50% no-repeat;
      background-size: contain;
    }
  }

  @media (max-width: 991px) {
    --stats-icon-size: 4rem;

    &:before {
      margin-inline-end: 2rem;
    }

    &--content {

    }

    &--title {
      font-size: 1.8rem;
    }

    &--value {
      font-size: 1.6rem;
    }


  }
}

.team-short-stats {
  display: flex;
  margin: 0 -2.5rem;

  &--item {
    max-width: 50%;
    padding: 0 2.5rem;
  }

  &--title {
    max-width: 100%;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    font-weight: 500;
    font-size: 1.8rem;
    line-height: 1.7;
    color: var(--brand-color);
    margin-bottom: 0.5rem;
  }

  &--value {
    max-width: 100%;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;

    font-size: 1.6rem;
    line-height: 1.7;
    color: var(--text-color-secondary);
  }

  @media (max-width: 991px) {
    margin: 0 -1.5rem;

    &--item {
      padding: 0 1.5rem;
    }

    &--title {
      font-size: 1.6rem;
    }

    &--value {
      font-size: 1.4rem;
    }
  }

}

.chart-block {
  &--title {
    margin-bottom: 3rem;
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.7;
  }

  @media (max-width: 991px) {
    &--title {
      font-size: 1.8rem;
      margin-bottom: 2rem;
    }
  }
}