.news {
  &--title {
    font-size: 3.2rem;
    line-height: 1.6;
    margin-bottom: 5rem;
    font-weight: 500;
    text-align: center;
  }

  &--content {

  }

  &--footer {
    margin-top: 5rem;
    text-align: center;
  }

  @media (max-width: 991px) {
    &--title {
      font-size: 1.8rem;
      margin-bottom: 4rem;
    }

    &--footer {
      margin-top: 4rem;
    }
  }
}

.news-list {
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  margin: 0 -2rem;
  list-style: none;
  margin: 0;
  padding: 0;

  &--item {
    padding: 0 2rem;
    margin-bottom: 4rem;
    width: 33.333333%;

    .news-item {
      height: 100%;
    }
  }

  @media (max-width: 991px) {
    flex-direction: column;
    margin: 0;

    &--item {
      width: 100%;
      padding: 0;

      &:not(:last-child) {
        margin-bottom: 4rem;
      }
    }

  }

}

.news-item {
  border-radius: var(--bodrder-radius);
  background-color: #ffffff;
  box-shadow: var(--shadow);
  position: relative;


  &--image {
    &-link {

    }

    img {
      width: 100%;
      max-width: 100%;
      aspect-ratio: 520 / 276;
      border-radius: var(--bodrder-radius) var(--bodrder-radius) 0 0;
    }
  }

  &--content {
    padding: 1rem 3rem 4rem;
  }

  &--meta {
    margin-bottom: 2rem;
    line-height: 1.7;
    font-size: 1.6rem;
  }

  &--date {
    font-weight: 500;
    color: var(--text-color-secondary);

  }

  &--title {
    font-size: 2.4rem;
    line-height: 1.7;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    color: var(--text-color);
    font-weight: 500;

    a {
      color: currentColor;

      &:hover {
        color: currentColor;
      }

    }
  }

  @media (max-width: 991px) {
    &--image {
      &-link {

      }
    }

    &--content {
      padding: 2rem;
    }

    &--meta {

    }

    &--date {

    }

    &--title {
      font-size: 1.8rem;
      line-height: 1.5;
    }
  }
}

.news-article {
  margin-bottom: 6rem;

  &--image {
    margin-bottom: 3rem;

    img {
      width: 100%;
      max-width: 100%;
      border-radius: var(--bodrder-radius);
    }
  }

  &--header {
    margin-bottom: 4rem;
  }

  &--meta {
    margin-bottom: 4rem;
  }

  &--date {
    color: var(--text-color-secondary);
    font-size: 1.8rem;
    line-height: 1.5;
  }

  &--title {
    font-size: 3.2rem;
    line-height: 1.6;
  }

  &--content {
    font-size: 1.8rem;
    line-height: 1.5;

    ul,
    ol,
    p {
      &:not(:last-child) {
        margin-bottom: 1em;
      }
    }

    li {
      &:not(:last-child) {
        margin-bottom: 0.5em;
      }
    }
  }

  @media (max-width: 991px) {
    &--image {
      margin-bottom: 2rem;
    }

    &--header {
      margin-bottom: 3rem;
    }

    &--meta {
      margin-bottom: 1rem;
    }

    &--date {
      color: var(--text-color-secondary);
      font-size: 1.4rem;
    }

    &--title {
      font-size: 2.4rem;
    }

  }
}