.my-sponsor {
  @extend .block-shadow;
  margin-bottom: 4rem;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  &--image {
    flex-shrink: 0;
    margin-inline-end: 6rem;

    img {
      width: 6.8rem;
      height: 6.8rem;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &--content {
    flex-grow: 1;
    min-width: 0;

  }

  &--data-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -3rem -2rem;
  }

  &--data {
    padding: 0 3rem 2rem;
  }

  &--title {
    font-size: 1.6rem;
    line-height: 1.6;
    color: var(--text-color-secondary);
  }

  &--value,
  &--name {
    font-size: 2rem;
    line-height: 1.6;
    color: var(--text-color);

    a {
      color: currentColor;
    }
  }

  &--nickname {

  }

  @media (max-width: 991px) {
    &--image {
      margin-inline-end: 2rem;
    }

    &--data-list {
      margin: 0;
      flex-direction: column;
    }

    &--data {
      padding: 0;
      margin: 0 0 3rem;
      max-width: 100%;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &--nickname,
    &--name,
    &--value {
      max-width: 100%;
      overflow-wrap: break-word;
    }
  }
}

.referals {

  &--header {
    margin-bottom: 2rem;
  }

  &--title {
    font-size: 2.4rem;
    font-weight: 500;
  }

  &--tree {
    @extend .block-shadow;
  }

  @media (max-width: 991px) {
    &--title {
      font-size: 1.8rem;
    }
  }

}

.tree-user-modal-button {
  display: block;
  font-size: 0;
  background: url(../img/more-vertical.svg) 50% 50% no-repeat;
  background-size: contain;
  width: 2.4rem;
  height: 2.4rem;
}

.tree-user-link {
  transition: all 0.3s;
  display: inline-block;

  &:hover {
    transform: translate(-0.2rem, -0.2rem);
  }

  &.disabled,
  &[disabled] {
    opacity: 0.5;
    filter: grayscale(1);

    &:hover {
      transform: none;
    }
  }

  @media (max-width: 991px) {
    &:hover {
      transform: none;
    }
  }

}

.structure-tree-item {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;

  &--controls {
    flex-shrink: 0;
  }

  &--level {
    min-width: 2.4rem;
    height: 2.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-shrink: 0;
    margin-inline-start: 0.5rem;
    font-size: 1.4rem;
    color: var(--text-color-secondary);
  }

  &--user-data {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;

    flex-grow: 1;
    min-width: 0;

    margin-inline-start: 3rem;
    margin-inline-end: 2rem;

    .usercard {
      min-width: 0;

      &--image {
        margin-inline-end: 1rem;

        img {
          width: 6rem;
          height: 6rem;
        }
      }

      &--nickname {
        font-size: 2rem;
      }

      &--name {
        font-size: 1.8rem;
      }

    }
  }

  &--team {
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    margin-top: 0.25em;
    margin-inline-start: 3rem;
    flex-shrink: 0;
    white-space: nowrap;

    &:before {
      content: '';
      display: block;
      background: url(../img/tree-team.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 1.5em;
      height: 1.5em;
      margin-inline-end: 0.625em;
      margin-top: -0.1em;
      flex-shrink: 0;
    }
  }

  &--meta {
    margin-inline-start: auto;

    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }

  &--total-value {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    overflow-wrap: break-word;
    max-width: 100%;
    text-align: right;

    .rtl & {
      text-align: left;
    }
  }

  &--links {
    margin: 0 -0.3rem;

    .tree-user-link {
      margin: 0 0.3rem;
      flex-shrink: 0;
    }
  }

  &--mobile-button {
    display: none;
  }

  @media (max-width: 991px) {

    &--controls {

    }

    &--level {

    }

    &--user-data {
      margin-inline-start: 1rem;

      .usercard {
        &--image {
          img {
            width: 3rem;
            height: 3rem;
          }
        }

        &--nickname {
          font-size: 1.4rem;
          font-weight: normal;
        }

        &--name {
          font-size: 1.4rem;
        }

      }
    }

    &--team {
      display: none;
    }

    &--meta {
      display: none;
    }

    &--mobile-button {
      display: block;
      flex-shrink: 0;
      margin-inline-start: auto;
    }


  }

}

.leaf-button {
  font-size: 0;
  cursor: pointer;
  display: block;
  background: url(../img/tree-plus.svg) 50% 50% no-repeat;
  background-size: contain;
  width: 2.4rem;
  height: 2.4rem;
  transition: all 0.3s;

  &.disabled,
  &[disabled] {
    opacity: 0.5;
    pointer-events: none;
    filter: grayscale(1);

    &:hover {
      transform: none;
    }
  }

  &.open {
    background: url(../img/tree-minus.svg) 50% 50% no-repeat;
    background-size: contain;
  }

  &:hover {
    transform: translate(-0.1rem, -0.1rem);
  }


  @media (max-width: 991px) {
    &:hover {
      transform: none;
    }
  }
}

.structure-tree-list {
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;

  $component: &;

  &--item {
    padding: 2rem 0;
    position: relative;
    border-bottom: 1px solid var(--items-border-color);

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
      border: none;
    }

    &-buttons {
      display: flex;
      align-items: center;

      .btn {
        min-width: 14.4rem;
        padding-left: 1rem;
        padding-right: 1rem;
        white-space: nowrap;

        &:not(:last-child) {
          margin-inline-end: 1rem;
        }

      }
    }

    #{$component} {
      #{$component}--item {
        border-bottom: none;
      }
    }

    > #{$component} {
      padding-left: 4.4rem;
      margin: 2rem 0;
      display: none;

      #{$component}--item {
        padding: 1.5rem 0;

        &:first-child {
          padding-top: 0;
        }
        &:last-child {
          padding-bottom: 0;
        }

        &:before {
          content: '';
          display: block;
          position: absolute;
          inset-inline-start: -3.2rem;
          width: 0;
          top: -3.5rem;
          bottom: 0;
          border-left: 1px solid var(--items-border-color);
        }

        &:last-child {
          //background-color: #ff0000;
          &:before {
            bottom: calc(100% - 1.2rem);
          }
        }

      }

    }

    &.open {

      > .structure-tree-item {
      }

      > #{$component} {
        display: block;
      }
    }

  }

  @media (max-width: 991px) {

    &--item {

      &-buttons {
        .btn {
          min-width: 0;
        }
      }

      > #{$component} {
        padding-left: 1.6rem;

        #{$component}--item {
          &:before {
            inset-inline-start: -0.4rem;
            top: -2.5rem;

          }

          &:last-child {
            &:before {

            }
          }
        }

      }
    }

  }


}

.tree-nav {
  margin-bottom: 2rem;

  .btn {
    min-width: 14.4rem;
  }
}

.user-details {

  .usercard {
    &--image {
      img {
        width: 3rem;
        height: 3rem;
      }
    }
  }

  &--header {
    margin-bottom: 2rem;
  }

  &--content {

  }

  &--stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1rem;
  }

  &--value {
    font-size: 1.8rem;
    min-width: 0;
    overflow-wrap: break-word;
  }

  &--team {
    display: flex;
    align-items: center;
    font-size: 1.6rem;
    margin-inline-start: 2rem;
    flex-shrink: 0;

    &:before {
      content: '';
      display: block;
      background: url(../img/tree-team.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 1.5em;
      height: 1.5em;
      margin-inline-end: 0.625em;
      margin-top: -0.1em;
      flex-shrink: 0;
    }
  }

  &--links {
    margin: 0 -0.7rem;

    .tree-user-link {
      margin: 0 0.7rem;
    }
  }
}