.balance-steps {
  text-align: center;
  margin: 4rem 0;

  @media (max-width: 991px) {
    margin: 0.6rem 0 4rem;
  }
}

.balance-steps-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  //flex-wrap: wrap;

  &--item {
    text-align: center;
    position: relative;
    color: var(--text-color-secondary);
    font-size: 1.2rem;

    $item: &;

    &:not(:last-child) {
      margin-inline-end: 7.6rem;

      &:after {
        content: '';
        display: block;
        background-color: var(--text-color-secondary);
        height: 1px;
        width: 5rem;
        position: absolute;
        inset-inline-start: calc(100% + 1.3rem);
        bottom: 0.6em;
      }
    }

    &-icon {
      margin-bottom: 1rem;
      width: 2.5rem;
      height: 2.5rem;
      margin-left: auto;
      margin-right: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid var(--text-color-secondary);

      border-radius: 50%;

      svg {
        //max-width: 100%;
      }
    }

    &-title {

    }

    &.is-active {
      color: var(--brand-color);

      #{$item}-icon {
        color: #ffffff;
        box-shadow: 0 0.3rem 0.7rem rgba(217, 4, 41, 0.32);
        background-color: var(--brand-color);
        border-color: var(--brand-color);
      }
    }

    &.is-passed {
      color: var(--text-color);

      #{$item}-icon {
        color: #ffffff;
        box-shadow: 0 0.3rem 0.7rem rgba(43, 45, 66, 0.15);
        background-color: var(--text-color);
        border-color: var(--text-color);
      }

      &:after {
        background-color: var(--text-color);
      }
    }
  }

  @media (max-width: 991px) {
    &--item {
      &:not(:last-child) {
        margin-inline-end: 6rem;

        &:after {
          width: 4rem;
          inset-inline-start: calc(100% + 1rem);
        }
      }
    }
  }

}

.balance-form {
  margin-left: auto;
  margin-right: auto;
  background-color: #ffffff;
  box-shadow: var(--shadow);
  border-radius: var(--bodrder-radius);
  padding: var(--block-padding);
  max-width: 52rem;

  &--title {
    text-align: center;
    font-size: 2.4rem;
    font-weight: 500;
  }

  .form-group {
    &:not(:first-child) {
      margin-top: 3rem;
    }

    &_flex {
      .icon {
        flex-shrink: 0;
        margin-inline-end: 1rem;
      }
    }

    > .control-label {
      margin-bottom: 2rem;
    }
  }

  .form-buttons {
    display: flex;
    justify-content: space-between;

    .btn {
      width: calc(50% - 1.5rem);
      padding-inline-start: 1.5rem;
      padding-inline-end: 1.5rem;
    }
  }

  @media (max-width: 991px) {
    max-width: none;

    &--title {
      margin-bottom: 2rem;
      font-size: 1.8rem;
    }

    .form-group {
      &:not(:first-child) {
        margin-top: 2rem;
      }

      > .control-label {
        margin-bottom: 1rem;
      }
    }

    .form-buttons {
      flex-direction: column;

      .btn {
        width: 100%;

        &:not(:last-child) {
          margin-bottom: 1rem;
        }
      }
    }
  }
}

.balance-node-notice {
  text-align: center;
  margin-top: 4rem;
  font-size: 1.6rem;

  .form-group & {
    margin: 0;
  }

  a {
    color: var(--brand-color);
  }

  @media (max-width: 991px) {
    margin-top: 2rem;
  }
}

@keyframes in-progress {
  100% {
    transform: rotate(1turn);
  }
}
.transaction-status {
  text-align: center;
  color: var(--text-color);

  &:before {
    margin-left: auto;
    margin-right: auto;
  }

  &.in-progress {

    &:before {
      content: '';
      display: block;
      background: url(../img/theme-status-progress.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 3rem;
      height: 3rem;
      margin-bottom: 1.5rem;
      animation: in-progress 1.5s linear infinite;
    }
  }

  &.ok {
    color: var(--success-color);

    &:before {
      content: '';
      display: block;
      background: url(../img/theme-status-ok.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 3rem;
      height: 3rem;
      margin-bottom: 1.5rem;
    }
  }

  &.error {
    color: var(--error-color);

    &:before {
      content: '';
      display: block;
      background: url(../img/theme-status-error.svg) 50% 50% no-repeat;
      background-size: contain;
      width: 3rem;
      height: 3rem;
      margin-bottom: 1.5rem;
    }
  }

}

.transaction-instructions {
  &--title {
    font-size: 2.4rem;
    font-weight: 500;
    line-height: 1.7;
  }

  &--description {
    color: var(--text-color-secondary);
  }

  @media (max-width: 991px) {
    font-size: 1.8rem;
  }
}

.transaction-instructions-list {
  color: var(--text-color-secondary);
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  line-height: 1.6;

  &--item {
    position: relative;
    padding-left: 1em;

    &:before {
      content: '';
      display: block;
      width: 0.3rem;
      height: 0.3rem;
      background-color: var(--text-color-secondary);
      border-radius: 50%;
      position: absolute;
      inset-inline-start: 0.4em;
      top: 0.7em;
    }
  }
}

.payway-details {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  &--icon {
    width: 6rem;
    flex-shrink: 0;
    margin-inline-end: 2rem;
    text-align: center;

    img {
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
  }

  &--content {

  }

  &--title {
    font-size: 1.6rem;
    line-height: 1.6;
    color: var(--text-color-secondary);
  }

  &--value {
    font-size: 1.8rem;
    line-height: 1.6;
  }
}

.transaction {

  .transaction-status {
    margin-bottom: 4rem;
  }

  &-info {
    margin-bottom: 3rem;

    &--title {
      font-weight: 500;
      font-size: 2.4rem;
      line-height: 1.7;
    }

  }

  &-details {
    padding: var(--block-padding);
    box-shadow: var(--shadow);
    border-radius: var(--bodrder-radius);
    margin-bottom: 3rem;

    &--block {
      border-bottom: 1px solid var(--items-border-color);
      padding: 2rem 0;

      &:first-child {
        padding-top: 0;
      }

      &:last-child {
        padding-bottom: 0;
        border-bottom: none;
      }

      &-title {
        font-size: 1.6rem;
        line-height: 1.6;
        color: var(--text-color-secondary);
        margin-bottom: 1rem;
      }

      &-value {
        font-size: 1.8rem;
        line-height: 1.6;
        overflow-wrap: break-word;
      }

    }
  }

  @media (max-width: 991px) {
    padding-top: 1rem;
    &-info {
      &--title {
        font-size: 1.8rem;
      }
    }
  }
}

.billing-stats {
  padding: var(--block-padding);
  border-radius: var(--bodrder-radius);
  background-color: #ffffff;
  box-shadow: var(--shadow);

  &--item {
    padding: 2rem 0;
    border-bottom: 1px solid var(--items-border-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 100%;

    * {
      max-width: 100%;
    }

    &:first-child {
      padding-top: 0;
    }

    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }

    &-content {

    }

    &-value {
      font-size: 2rem;
      margin-inline-start: 2rem;
    }

    &-header {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: 1rem;
    }

    &-name {
      font-size: 1.8rem;
      overflow-wrap: break-word;
      max-width: 100%;

      a {
        color: currentColor;
      }

      .wallet {
        overflow-wrap: break-word;
        max-width: 100%;
      }
    }

    &-status {
      margin-inline-start: 2rem;
      font-size: 1.6rem;
    }

    &-meta {

    }

    &-date {
      font-size: 1.6rem;
      color: var(--text-color-secondary);
    }

    &.empty {
      color: var(--text-color-secondary);
      justify-content: center;
    }

  }

  @media (max-width: 991px) {
    &--item {
      flex-direction: column;
      align-items: flex-start;

      &-value {
        margin-inline-start: 0;
        margin-top: 1rem;
      }

      &-header {
        flex-direction: column;
        align-items: flex-start;
      }

      &-status {
        margin-inline-start: 0;
      }
    }
  }
}