.case-breadcrumbs-list {
  list-style: none;
  margin: 0;
  padding: 0;
  color: var(--text-color);
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;

  &--item {

    $item: &;

    &:first-child {

      #{$item}-link {
        &:hover {
          text-decoration: none;
        }
      }

    }

    &:not(:first-child) {
      margin-inline-start: 1rem;
      position: relative;

      &:before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        margin-inline-end: 1rem;
        width: 0.3rem;
        height: 0.3rem;
        border-radius: 50%;
        background-color: var(--text-color);
      }
    }

    &-link {
      color: currentColor;

      &:hover {
        color: currentColor;
      }

      .icon {
        width: 1.7em;
        height: 1.7em;
      }
    }
  }
}

.cases-menu {
  max-width: 47.6rem;
  margin-left: auto;
  margin-right: auto;
}

.case-content {
  margin-top: 4rem;

  $component: &;

  &--row {
    margin: 0 -2rem 2rem;
    display: flex;

    > * {
      padding: 0 2rem;
    }
  }

  &--sidebar {
    width: 26rem;
    flex-shrink: 0;
  }

  &--content {
    flex-grow: 1;
    min-width: 0;

    &-case {
      & + & {
        margin-top: 6rem;
      }

      #{$component}--header {
        margin-bottom: 2rem;
      }

    }

    &-footer {
      margin: 4rem 0;
    }
  }

  &--header {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .page-title {
      margin: 0;
      margin-inline-end: 4rem;
      flex-shrink: 0;
    }

    #{$component}--breadcrumbs {
      flex-grow: 1;
      min-width: 0;
      margin: 0;
      margin-inline-end: 4rem;
    }

    .cases-menu {
      flex-shrink: 0;
      flex-grow: 1;
      margin: 0;
      //margin-inline-start: auto;
    }

  }

  &--breadcrumbs {
    margin-bottom: 4rem;

  }

  &--title {
    font-size: 2rem;
    line-height: 1.6;
  }

  @media (max-width: 991px) {

    &--row {
      margin-left: 0;
      margin-right: 0;

      > * {
        padding: 0;
        width: 100%;
      }
    }

    &--title {
      text-align: center;
    }

    &--row {
      flex-direction: column;
    }

    &--sidebar {
      margin-bottom: 2rem;
      width: 100%;
    }

    &--content {
      &-footer {
        margin-top: 2rem;
      }

      &-case {
        & + & {
          margin-top: 4rem;
        }
      }
    }

    &--header {
      flex-direction: column;
      align-items: center;

      .page-title {
        order: 1;
        margin: 0 0 2rem;
      }

      #{$component}--breadcrumbs {
        order: 3;
        margin: 0;
      }

      .cases-menu {
        order: 2;
        margin: 0 0 4rem;
        width: 100%;
      }

    }

  }



}

.cases-nav {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: var(--text-color);
  padding: 0;
  margin: 0;

  &--item {
    $item: &;
    position: relative;
    width: 100%;

    &:not(:last-child) {
      margin-bottom: 2rem;
    }

    &-link {
      display: block;
      color: currentColor;
      padding: 2rem;
      background: #FFFFFF;
      border: 1px solid var(--items-border-color);
      box-shadow: var(--shadow);
      border-radius: 3rem;
      font-weight: 500;
      font-size: 2.2rem;
      line-height: 1.25;
      text-align: center;
      white-space: nowrap;
      overflow: hidden;
      -ms-text-overflow: ellipsis;
      text-overflow: ellipsis;

      > span {
        display: block;
      }

      &:hover {
        text-decoration: none;
      }

      &.disabled,
      &[disabled] {
        opacity: 0.5;
        pointer-events: none;
      }

    }

    &.is-active {
      #{$item}-link {
        border: 2px solid var(--brand-color);
      }
    }

  }

  @media (max-width: 991px) {
    flex-direction: row;
    margin: 0 -1.5rem;
    padding: 0 1rem 1rem;
    align-items: stretch;
    width: calc(100% + 3rem);
    overflow-x: auto;
    overflow-y: visible;

    &--item {
      flex: 1;
      padding: 0 0.5rem;

      &:not(:last-child) {
        margin-bottom: 0;
      }

      &-link {
        font-size: 1.2rem;
        line-height: 1;
        height: 100%;
        border-radius: 1.2rem;
        padding: 1rem;
        font-weight: normal;

        > span {
          font-weight: 500;
          font-size: 1.4rem;
          margin-top: 0.6rem;

          span {
            display: block;
          }
        }
      }
    }
  }
}

.case {
  position: relative;
  background-color: #ffffff;
  border-radius: var(--bodrder-radius);
  box-shadow: var(--shadow);
  border: 1px solid var(--items-border-color);
  width: 100%;

  $component: &;

  + .case {
    margin-top: 4rem;
  }

  &--row {
    display: flex;
    align-items: stretch;

    //display: grid;
    //grid-auto-flow: column;
    //grid-auto-columns: 1fr;

    &:not(:last-child) {
      border-bottom: 2px solid var(--items-border-color);
    }

    &.row-2 {
      #{$component}--item {
        width: 50%;
      }
    }
    &.row-3 {
      #{$component}--item {
        width: 33.33333%;
      }
    }
    &.row-4 {
      #{$component}--item {
        width: 25%;
      }
    }
  }

  &--item {
    width: 100%;
    &:not(:last-child) {
      border-inline-end: 2px solid var(--items-border-color);
    }

  }


}

.case-item {
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 2.5rem;
  max-width: 100%;
  position: relative;
  overflow-x: hidden;

  $component: &;

  &:hover {
    text-decoration: none;
    color: var(--brand-color);

    #{$component}--parent {
      visibility: visible;
      opacity: 1;
      color: var(--text-color);
    }
  }

  > * {
    display: block;
  }


  &--image {

    img {
      width: 8rem;
      height: 8rem;
      border-radius: 50%;
      object-fit: cover;

      &[src*=".svg"] {
        box-shadow: var(--shadow);
      }

      .row-4 & {

        @media (max-width: 991px) {
          width: 5rem;
          height: 5rem;
        }

      }
    }

  }

  &--name {
    font-weight: 500;
    font-size: 2.4rem;
    line-height: 1.2;
    margin-top: 2rem;
    //max-width: 100%;
    //overflow: hidden;
    //-ms-text-overflow: ellipsis;
    //text-overflow: ellipsis;
    overflow-wrap: break-word !important;
    max-width: 100%;
    text-align: center;


    span {
      display: block;
    }

    span {
      display: block;
    }
  }

  &--parent {
    position: absolute;
    z-index: 1;
    top: 8.7rem;
    left: 50%;
    transform: translateX(-50%);
    background: #FFFFFF;
    box-shadow: var(--shadow);
    border-radius: 100px;
    padding: 1rem 1.5rem;
    font-size: 1.6rem;
    line-height: 1;
    transition: opacity 0.3s, visibility 0.3s;
    max-width: 95%;
    white-space: nowrap;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    opacity: 0;
    visibility: hidden;
  }

  &.empty {
    opacity: 0.3;
  }

  @media (max-width: 991px) {
    padding: 1.5rem;

    &--name {
      font-size: 1.8rem;
    }

  }
}

.case-notice {
  font-size: 1.6rem;
  line-height: 1.6;
  width: max-content;
  max-width: 100%;
  margin-left: auto;margin-right: auto;
  text-align: left;
  padding: 1rem;

  background: rgba(217, 4, 41, 0.05);
  border-radius: 3rem;

  .rtl & {
    text-align: right;
  }

  @media (max-width: 991px) {
    padding: 2rem;
  }

}

.case-success {
  text-align: center;
  line-height: 1.6;
  margin: 4rem 0;

  $component: &;

  &:before {
    content: '';
    display: block;
    background: url(../img/case-success.svg) 50% 50% no-repeat;
    background-size: contain;
    width: 13.8rem;
    height: 13.8rem;
    margin-bottom: 3.7rem;
    margin-left: auto;
    margin-right: auto;
  }

  &--title {
    font-weight: 500;
    font-size: 3.2rem;

    margin-bottom: 2rem;
  }

  &--description {
    color: var(--text-color-secondary);
    font-size: 1.8rem;
  }

  &_empty {

    &:before {
      display: none;
    }

    #{$component}--title {
      color: var(--text-color-secondary);
    }
  }

  @media (max-width: 991px) {
    &--title {
      font-size: 2.4rem;
    }
  }

}