.toast {
  width: 35rem;
  font-size: 1.6rem;
  border: none;
  border-radius: 1.2rem;
  background-color: #ffffff;
  box-shadow: var(--shadow-sm);

  $component: &;

  &-header {
    color: var(--text-color);
    background: none;
    padding: 2rem 2rem 1rem;
    border: none;
    align-items: center;
  }

  &-title {
    font-size: 1.8rem;
    font-weight: 500;
    margin-inline-end: auto;
  }

  &-time {
    color: var(--text-color-secondary);
    font-size: 1.4rem;
    flex-shrink: 0;
  }

  &-body {
    background: none;
    padding: 1rem 2rem 2rem;
  }

  &-buttons {
    margin-top: 0.5em;
  }

  &-info {
    #{$component}-header {
      &:before {
        content: '';
        display: block;
        flex-shrink: 0;
        margin-inline-end: 0.5em;
        width: 1.5em;
        height: 1.5em;
        border-radius: 50%;
        background: var(--blue-color);
      }
    }
  }
  &-error {
    #{$component}-header {
      &:before {
        content: '';
        display: block;
        flex-shrink: 0;
        margin-inline-end: 0.5em;
        width: 1.5em;
        height: 1.5em;
        border-radius: 50%;
        background: var(--error-color);
      }
    }
  }
  &-success {
    #{$component}-header {
      &:before {
        content: '';
        display: block;
        flex-shrink: 0;
        margin-inline-end: 0.5em;
        width: 1.5em;
        height: 1.5em;
        border-radius: 50%;
        background: var(--success-color);
      }
    }
  }


}
.gtng-toasts {
  position: fixed;
  z-index: 501;
  top: 1rem;
  inset-inline-end: 1rem;
}
